.app {
	min-height: 100vh;
	display: flex;
	flex-direction: column;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: 'Roboto Slab', 'Nunito', sans-serif;
}

:root {
	--beige: rgb(248, 237, 227);
	--light-green: rgb(189, 210, 182);
	--mid-green: rgb(162, 178, 159);
	--dark-green: rgb(121, 135, 119);
	--default-shade: inset 0 0 0 1000px rgba(0, 0, 0, 0.4);
}