.btn {
	padding: 8px 20px;
	border-radius: 2px;
	outline: none;
	border: none;
	cursor: pointer;
}

.btn--primary {
	background-color: var(--beige);
	color: black;
	border: 1px solid var(--beige);
}

.btn--outline {
	background-color: transparent;
	color: var(--beige);
	padding: 8px 20px;
	border: 1px solid var(--beige);
	transition: all 0.3s ease-out;
}

.btn--medium {
	padding: 8px 20px;
	font-size: 20px;
	transition: all 0.3s ease-out;
}

.btn--large {
	padding: 12px 26px;
	font-size: 20px;
	transition: all 0.3s ease-out;
}

.btn--medium:hover, .btn--large:hover {
	background: var(--beige);
	color: var(--mid-green);
}