.form-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	text-align: center;
	padding: 4rem;
	background: rgba(0, 0, 0, 0.4);
	color: var(--beige);
	min-height: 20rem;
}

.form-wrapper > h1 {
	padding: 2rem;
}

.sign-up-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem;
}

.sign-up-form > h1 {
	padding-bottom: 1rem;
}

.inputs-wrapper {
	display: flex;
	flex-direction: row;
}

.email-input {
	border-radius: 5px;
	height: 40px;
	font-size: 1.5rem;
	background: var(--dark-green);
	color: var(--beige);
	padding: 10px;
	appearance: none;
	border: none;
}

::placeholder {
	color: rgb(200, 200, 200);
}

.subscribe-button {
	border-radius: 5px;
	height: 40px;
	font-size: 1.4rem;
	background: var(--dark-green);
	color: var(--beige);
	padding: 5px;
	appearance:none;
	border:none;
	margin-left: 0.5rem;
	transition: all 0.2s ease-out;
}

.subscribe-button:hover {
	cursor: pointer;
	background: var(--beige);
	color: var(--dark-green);
}

@media screen and (max-width: 500px) {
	.inputs-wrapper {
		flex-direction: column;
		align-items: center;
	}

	.email-input {
		margin-bottom: 0.5rem;
	}

	.subscribe-button {
		width: 80%;
		margin-left: 0;
	}
}